<template>
  <div class="center requirements">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
      <!-- <div class="center_content_search">
        <div class="center_content_search_left">
                <div class="search-input-suffix">
                    <template v-for="(vo,index) in versionOptions">
                         <el-button @click="search(vo,index)" class="versions-btn" :class="{' versions-gray': index !== activeIndex}" type="primary" :key="vo.id">{{vo.name}}</el-button>
                    </template>
                </div>
        </div>
        <div class="center_content_search_right">
          <el-button @click="openAdd" type="primary" icon="el-icon-plus" round>新建</el-button>
        </div>
      </div> -->
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <ul>
                <li class="targetItem" v-for="(item, index) in props.row.chlids" :key="index" :title="item.content">
                    <div class="index">
                      <img src="@/assets/img/bianhao.png" alt="">
                      <span>{{item.code}}</span>
                    </div>
                    <div style="padding-left: 60px">
                      <div class="itemContent">指标点{{item.code}}：{{item.content}}</div>
                      <div style="overflow: hidden">
                        <!-- <div style="margin: 0 10px 10px 0;  color: #aaa;text-align: center; float: left; " v-for="(co , index) in item.courses" :key="index">{{co.course_name}}</div> -->
                      </div>
                    </div>
                </li>
              </ul>
            </template>
          </el-table-column>
          <el-table-column prop="code" label="编号" align="center" width="80"></el-table-column>
          <el-table-column prop="content" label="毕业要求内容"></el-table-column>
          <el-table-column prop="name" label="毕业要求简称" width="220"></el-table-column>
          <el-table-column prop="goalVal" label="支撑目标" width="220"></el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <div class="operate-button distance">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>
                <el-button @click.native.prevent="editRow(scope.row)" type="text" size="small">编辑</el-button>
              </div>
              <div class="operate-button distance">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>
                <el-button style="color: #ff5858" @click.native.prevent="deleteRow(scope.row)" type="text" size="small">删除</el-button>
              </div>
              <!-- <div class="operate-button">
                <SvgIcon icon-class="iconfenjie"></SvgIcon>
                <el-button
                  @click.native.prevent="disintegrateRow(scope.row)"
                  type="text"
                  size="small"
                >分解</el-button>
              </div> -->
            </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="data.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="130px" style="padding-right: 50px">
        <el-form-item label="培养方案版本" prop="versionId">
          <el-select v-model="form.versionId" @change="versionSelect" placeholder="请选择" disabled>
            <el-option
              v-for="item in versionOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支撑目标" prop="goalSuIDs">
          <el-select v-model="form.goalSuIDs" multiple placeholder="请选择">
            <el-option
              v-for="item in goalAllOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="毕业要求编号" prop="code">
          <el-input v-model="form.code" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="毕业要求内容" prop="content">
          <el-input v-model="form.content" type="textarea" :rows="2" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="毕业要求简称" prop="name">
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="毕业要求指标点" prop="point">
          <el-row v-for="(item, index) in this.form.indexPoints" :key="index" style="position: relative;margin-bottom: 20px">
            <el-col :span="2">
              <div style="width:50px;border: 1px solid #dcdfe6; text-align: center; border-radius: 5px; background-color: #f8f8f8">{{index+1}}</div>
            </el-col>
            <el-col :span="21" :offset="1">
              <el-input v-model="item.content" placeholder="请输入"></el-input>
            </el-col>
            <i v-if="index!==0" @click="deletePoint(index)" style="font-size: 21px;position: absolute; right: -30px; top: 50%; transform: translateY(-50%); cursor: pointer; color: #aaa" class="el-icon-circle-close"></i>
          </el-row>
          <div @click="addPoint" style="cursor: pointer">
            <!-- <SvgIcon style="float: left;margin: 10px 10px 0 0;" icon-class="icontianjia" size="20px"></SvgIcon> -->
            <i class="el-icon-plus" style="color:#03b187;font-weight: 700;"></i>
            <span>添加毕业要求指标点</span>
          </div>
        </el-form-item>
        <!-- <el-form-item label="目标达成值" prop="minimum">  
                 <el-input-number v-model="form.minimum" controls-position="right"></el-input-number>
        </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')" :disabled="isSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
export default {
  name: "GraduationRequirements",
  data() {
    const checkMinimum = (rule, value, callback) => {
      if (value === 0 || !value) {
        return callback(new Error("请输入目标达成值"));
      }
      if (value < 0 || value > 1) {
        return callback(new Error("目标达成值范围0-1之间"));
      } else {
        callback();
      }
    };
    const checkPoint = (rule, value, callback) => {
      let num = 0;
      this.form.indexPoints.forEach(item => {
        if (!item.content) {
          num++;
        }
      });
      if (num > 0) {
        return callback(new Error("请输入课程目标及课程目标内容"));
      } else {
        callback();
      }
    }
    return {
      pageTitle: "毕业要求",
      pageTitleArr: [],
      data: {},
      tableData: [],
      versionOptions: [],
      goalAllOptions: [],
      activeIndex: 0,
      isAdd: true,
      isSubmit:false,
      userDialogFormVisible: false,
      page: {
        versionId: "",
        page: 1,
        rows: 15
      },
      form: {
        name: "",
        versionId: "",
        goalSuIDs: [],
        code: "",
        content: "",
        //minimum:null,
        id: undefined,
        indexPoints: [
          {content: ''}
        ]
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入毕业要求名称",
            trigger: "blur"
          }
        ],
        versionId: [
          {
            required: true,
            message: "请选择培养方案版本",
            trigger: "blur"
          }
        ],
        goalSuIDs: [
          {
            required: true,
            message: "请选择培养目标",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入毕业要求编号",
            trigger: "blur"
          }
        ],
        minimum: [
          {
            required: true,
            validator: checkMinimum,
            trigger: "blur"
          }
        ],
        point: [
          {
            required: true,
            validator: checkPoint,
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {},
  mounted() {
    this.versionAll();
    this.getList();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加毕业要求" : "编辑毕业要求";
    }
  },
  methods: {
    addPoint() {
      this.form.indexPoints.push(
        {content: ''}
      )
    },
    deletePoint(index) {
      this.form.indexPoints.splice(index, 1)
    },
    getList() {
      if (localStorage.getItem("versionId")) {
        this.page.versionId = localStorage.getItem("versionId");
        this.initData();
      } else {
        setTimeout(() => {
          this.getList();
        }, 200);
      }
    },
    initData() {
      const url = trainingProgramApi.claimList;
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            this.data = res;
            this.tableData = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    versionAll() {
      const url = trainingProgramApi.versionAll;
      this.$get(url)
        .then(res => {
          //  console.log(res,'版本')
          if (res.status === 1) {
            this.versionOptions = res.data;
            // this.versionOptions.forEach(item=>{
            //     console.log(item.id,'id')
            // })
            // this.page.versionId=res.data[0].id;
            // this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    goalAll(id) {
      const url = trainingProgramApi.goalAll({
        versionId: id
      });
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.goalAllOptions = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    headInfo(item) {
      this.pageTitle = item[1];
      this.pageTitleArr = item;
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    search(vo, index) {
      this.activeIndex = index;
      this.page.page = 1;
      this.page.versionId = vo.id;
      localStorage.setItem("versionIdss", vo.id);
      this.initData();
    },
    openAdd() {
      this.userDialogFormVisible = true;
      this.$nextTick(() => {
        this.form = {
          name: "",
          versionId: localStorage.getItem("versionId"),
          goalSuIDs: [],
          code: "",
          content: "",
          // minimum:null,
          id: undefined,
          indexPoints: [
            {content: ''}
          ]
        };
        this.goalAll(this.form.versionId);
        this.isAdd = true;
        this.isSubmit=false;
        this.$refs["form"].resetFields();
      });
    },
    versionSelect(val) {
      this.form.goalSuIDs = [];
      this.goalAll(val);
    },
    deleteRow(row) {
      this.$confirm("确定删除该毕业要求?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      this.goalAll(row.versionId);
      let arr = []
      row.chlids.forEach(item => {
        arr.push({content: item.content})
      })
      const obj = {
        name: row.name,
        versionId: row.versionId,
        goalSuIDs: row.goalSuIDs,
        code: row.code,
        content: row.content,
        // minimum:row.minimum,
        id: row.id,
        indexPoints: arr
      };
      this.form = obj;
      this.userDialogFormVisible = true;
      this.isAdd = false;
      this.isSubmit=false;
    },
    disintegrateRow(row) {
      this.$router.push({
        path: "/trainingProgram/disintegrate",
        query: {
          title: row.name,
          code: row.code,
          id: row.id,
          pageTitle: this.pageTitleArr
        }
      });
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isSubmit=true;
          this.form.indexPoints.forEach((item, index) => {
            item.code = index + 1
          })
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    userAdd() {
      const url = trainingProgramApi.claimAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          this.isSubmit=false;
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.claimDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = trainingProgramApi.claimEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          this.isSubmit=false;
          console.log(err);
        });
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.requirements {
  .el-table__expanded-cell[class*=cell] {
    background-color: #fcfcfc;
  }
  .el-col-offset-1{
    margin-left: 0.5%;
  }
  ul {
    overflow: hidden;
    .targetItem {
      box-sizing: border-box;
      overflow: hidden;
      margin-bottom: 20px;
      border-radius: 10px;
      .index {
        width: 44px;
        height: 44px;
        text-align: center;
        line-height: 44px;
        float: left;
        position: relative;
        margin-right: 20px;
        span {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          white-space: nowrap;
        }
      }
      .itemContent {
        margin-bottom: 10px;
        line-height: 2;
        color: #323232;
        overflow:hidden;
        font-size:14px;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;//控制行数
      }
    }
  }
}
</style>



